
        const data = [{"#":"","title":"ARtillery","---":"","description":"\\n\\nЗадача игрока — продержаться как можно дольше, отстреливаясь от противников, появляющихся в пространстве вокруг него. Для защиты игрок может перемещать и наклонять телефон, а также сбивать летящие в него снаряды. Для стрельбы нужно коснуться экрана. Со временем сложность увеличивается. Существует три типа противников с разными моделями поведения и сложностью, каждый из которых появляется реже или чаще других.\\n\\nПриложение реализовано с использованием ARCore, что позволяет ему работать без привязки к <span style=\"font-style:italic;\">печатным</span> меткам.\\n\\n<small>\\nПечатные метки — это распечатанные на бумаге или нарисованные ярким цветом специальные символы, которые являются опорным маркером для камеры. <a target=\"_blank\" href=\"https://habr.com/ru/post/563666/?\">Подробнее</a>\\n\\nРанняя версия приложения была реализована с использованием Vuforia, для работы требовалось распечатать метку.\\n</small>","images":["/images/projects/ARtillery/resized_ARtillery_Moment-min.webp","/images/projects/ARtillery/resized_ARtillery_Moment3.webp","/images/projects/ARtillery/resized_ARtillery_Moment2-min.webp","/images/projects/ARtillery/ARtilleryAction.gif"],"videos":["https://youtu.be/BIoWlE25bD4"],"tags":["Игра","AR","Unity","Android"],"tech":["Unity"],"developers":["Поздняков Д.В."],"sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/ARtillery"}],"link":"ARtillery"},{"#":"","title":"Многопользовательская сборка ракетного двигателя","---":"","description":"\\n\\n\\nИгроки собирают ракетный двигатель серии CFM-56 в VR-пространстве.\\n\\nКаждый этап сборки сопровождается руководствами и подсказками, все детали документированы и подробно описаны. Собранная модель анимирована и демонстрирует принципы своей работы.\\n\\nПроект является демонстрацией процесса обучения специалистов по оборудованию в VR-пространстве: погружение позволяет быстро освоить материал без риска порчи настоящего оборудования и получения травм.\\n\\n","images":["/images/projects/Engine-VR/resized_s4.webp","/images/projects/Engine-VR/resized_s3.webp","/images/projects/Engine-VR/resized_s2.webp","/images/projects/Engine-VR/resized_s1.webp","/images/projects/Engine-VR/resized_141155619-bb28a0e8-fd3b-4359-97e5-36f3d1cf71c3.webp","/images/projects/Engine-VR/resized_141160470-a31d20dd-86bd-4be8-9308-a631227b5ba0.webp","/images/projects/Engine-VR/resized_141155697-f247130d-e9a9-4f7d-812c-7a328e3022af.webp","/images/projects/Engine-VR/resized_141155722-2e5c8eab-dfef-42f7-ae67-daf43e1756d6.webp"],"videos":["https://youtu.be/6FTMDRp5fN4"],"tags":["VR","Взаимодействие","Образовательная"],"tech":["Unity","C#"],"developers":["Сиротенко М.Р.","Корчиков М.Д.","Смирнов М.А","Лысаков И.А.","Смирнов Я.А."],"site":"","sourcecode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/Engine-VR"}],"link":"Engine-VR"},{"#":"","title":"GameCenter (система рекордов)","---":"","description":"\\n\\nСистема предназначена для формирования очереди на выставочные стенды, для контроля посещаемости проектов лаборатории ИТ и предоставления игровой статистики пользователей. Также предусмотрено администрирование  рекордов пользователей, игр, очередей.","images":["/images/projects/GameCenter-Front/resized_1.webp","/images/projects/GameCenter-Front/resized_2.webp","/images/projects/GameCenter-Front/resized_3.webp","/images/projects/GameCenter-Front/resized_4.webp"],"videos":[],"tags":["Frontend","Информационная система","Обработка данных"],"tech":["Angular","Asp.net core"],"developers":["Китанин С.C.","Южаков В.Е."],"site":"","sourcecode":[{"name":"Фронтенд","link":"https://github.com/RTUITLab/GameCenter-Front"},{"name":"Бэкенд","link":"https://github.com/RTUITLab/GameCenter-Backend"}],"link":"GameCenter-Front"},{"#":"","title":"GeoHelper","---":"","description":"\\n\\n\\nПроект для реализации AR-слоя мира, в котором отображаются объекты  дополненной реальности в зависимости от заданного географического  положения.\\n\\nПользователи могут «привязывать» информацию к географическим объектам с помощью телефона, а после просматривать её. Простейшие примеры использования: AR-подсказки для навигации, исторические справки о памятниках архитектуры, справочная информация для коммунальных служб.\\n\\nПроект состоит из сайта и приложений для платформ Android и IOS.\\n\\nСайт предназначен для управления внесёнными данными. Мобильное приложение служит для отображения AR-слоя мира на  экране используемого устройства.\\n\\n","images":["/images/projects/GeoHelperUnity/resized_s1.webp","/images/projects/GeoHelperUnity/resized_s2.webp","/images/projects/GeoHelperUnity/resized_s3.webp","/images/projects/GeoHelperUnity/resized_s4.webp","/images/projects/GeoHelperUnity/resized_s5.webp","/images/projects/GeoHelperUnity/resized_s6.webp","/images/projects/GeoHelperUnity/resized_s7.webp"],"videos":[],"tags":["Frontend","Backend","Mobile","AR"],"tech":["Vue","Node.js","Unity","AR Foundation","C#"],"developers":["Лаптев И.А.","Китанин С.С.","Быковская О.C.","Смирнов Я.А."],"site":"","sourcecode":[{"name":"Фронтенд","link":"https://github.com/RTUITLab/GeoHelper-Front"},{"name":"Бэкенд","link":"https://github.com/RTUITLab/GeoHelper-Back"},{"name":"Unity проект (приложение для Android или Ios)","link":"https://github.com/RTUITLab/GeoHelperUnity"}],"link":"GeoHelperUnity"},{"#":"","title":"GyroBall","---":"","description":"\\n\\nИгра для проверки удобства использования механики акселерометра.\\n\\nИгра состоит из уровней-лабиринтов с препятствиями, через которые необходимо прокатить шар. Управление — наклонами телефона. Для шарика доступно 4 различные механики, соответствующие стихиям: вода, земля, огонь, воздух.\\n\\nЛокации соответствуют выбранной стихийной тематике.","images":["/images/projects/GyroBall/resized_21.webp","/images/projects/GyroBall/resized_22.webp","/images/projects/GyroBall/resized_24.webp","/images/projects/GyroBall/resized_25.webp","/images/projects/GyroBall/resized_Снимок.webp","/images/projects/GyroBall/resized_Снимок2.webp","/images/projects/GyroBall/resized_Снимок3.webp","/images/projects/GyroBall/resized_Снимок4.webp","/images/projects/GyroBall/resized_Снимок5.webp","/images/projects/GyroBall/resized_Снимок22.webp","/images/projects/GyroBall/resized_1.webp","/images/projects/GyroBall/resized_21.webp","/images/projects/GyroBall/resized_2.webp","/images/projects/GyroBall/resized_3.webp"],"videos":[],"tags":["Игра","Аркада","Mobile"],"tech":["C#","3D modeling","Unity 3D"],"developers":["Баканова М.В.","Новаков Н.Ю."],"site":"","sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/GyroBall"}],"link":"GyroBall"},{"#":"","title":"ITLab (система управления лабораторией)","---":"","description":"\\n\\nДля организации работы людей в лаборатории создана система, учитывающая активность людей, в рамках деятельности лаборатории. Система позволяет распределять нагрузку, обеспечивать прозрачность выполняемых действий и мероприятий, контролировать оплату работы.\\n\\nДля использования системы можно использовать браузер, либо специальные мобильные приложения. Поддерживаются уведомления с помощью ботов в социальных сетях и почты.","images":["/images/projects/ITLab/resized_s1.webp","/images/projects/ITLab/resized_s2.webp","/images/projects/ITLab/resized_s3.webp","/images/projects/ITLab/resized_s4.webp","/images/projects/ITLab/resized_1.webp","/images/projects/ITLab/resized_2.webp"],"videos":[],"tags":["Информационная система","Frontend","Backend","Mobile"],"tech":["Vue","ASP","Ktor","Swift"],"developers":["Макущенко М.А.","Романов Д.Е.","Комар Б.Г.","Кузнецов А.А.","Иванов М.Е."],"site":"","sourcecode":[{"name":"Фронтенд","link":"https://github.com/RTUITLab/ITLab-Front"},{"name":"Система событий/оборудования","link":"https://github.com/RTUITLab/ITLab-Back"},{"name":"Система уведомлений","link":"https://github.com/RTUITLab/ITLab-Notify"},{"name":"Система записей о работе","link":"https://github.com/RTUITLab/ITLab-Reports"},{"name":"Система учета зарплат","link":"https://github.com/RTUITLab/ITLab-Salary"},{"name":"Приложение для iOS","link":"https://github.com/RTUITLab/ITLab-iOS"}],"link":"ITLab"},{"#":"","title":"Сервис проектов RTUITLab","---":"","description":"\\n\\nСервис проектов является отдельным модулем, интегрированным в систему управления лабораторией.\\n\\nСервис позволяет отслеживать проекты лаборатории, мониторить процесс их выполнения, контроллировать выполнение задач каждой выпускаемой версии. Присутствует возможность хранить файлы, относящиеся к проекту, оставлять комментарии, публиковать новости. Дополнительно реализована система ролей и ведения документации.","tags":["Backend","Golang"],"tech":["Go","MongoDB"],"developers":["Демин Д.Д.","Миронов Н.М.","Лаптев И.А.","Корольков А.Д.","Баикин К.Е."],"images":["/images/projects/ITLab-Projects/resized_list.webp","/images/projects/ITLab-Projects/resized_show_ArtWay.webp","/images/projects/ITLab-Projects/resized_show_ArtWay2.webp","/images/projects/ITLab-Projects/resized_show_VIKA.webp","/images/projects/ITLab-Projects/resized_edit_VIKA.webp"],"videos":[],"site":"https://manage.rtuitlab.dev/projects","sourcecode":[{"name":"Бекенд","link":"https://github.com/RTUITLab/ITLab-Projects"},{"name":"Фронтенд","link":"https://github.com/RTUITLab/ITLab-Projects-Front"}],"link":"ITLab-Projects"},{"#":"","title":"Owl Cabin — Летай-созерцай-собирай-улучшай","---":"","description":"\\n\\nУправляем самолётом от третьего лица. Собираем звёздочки, улучшаем самолёт, набираем очки.\\n\\nКрасивая, спокойная музыка, красочные локации, спокойный полёт.","images":["/images/projects/OwlCabin/resized_s6.webp","/images/projects/OwlCabin/resized_s2.webp","/images/projects/OwlCabin/resized_s3.webp","/images/projects/OwlCabin/resized_s4.webp","/images/projects/OwlCabin/resized_s5.webp","/images/projects/OwlCabin/resized_s1.webp","/images/projects/OwlCabin/resized_s7.webp"],"videos":[],"tags":["VR","Game","Aircraft","Relax","Easy play"],"tech":["Unity","Google Cardboard"],"developers":["Миронов Н. М.","Иерусалимов И. Д.","Корчиков М. Д.","Валяев Д. А.","Шошников И. К."],"site":"","sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/OwlCabin"}],"link":"OwlCabin"},{"#":"","title":"Plane crash simulator","---":"","description":"\\n\\nСимулятор, помещающий пользователя в ситуацию, когда он находится в падающем самолете. Самолет находится в турбулентности и постепенно наклоняется вперед с раскачкой в разные стороны.","images":["/images/projects/PlaneCrashSimulator/resized_1.webp","/images/projects/PlaneCrashSimulator/resized_2.webp","/images/projects/PlaneCrashSimulator/resized_3.webp"],"videos":[],"tags":["VR","Симулятор"],"tech":["Unity","Steam VR","C#"],"developers":["Валяев Д.А.","Шорин И.А."],"site":"","sourcecode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/PlainCrashSimulator"}],"link":"PlaneCrashSimulator"},{"#":"","title":"ЗИЛ","---":"","description":"\\n\\nИгра для кресла [Futurift](https://6-dof.ru/futurift/).\\n\\nОтрыв от преследования в автомобиле, в зоне боевых действий. Стрельба и оружие прилагаются.","images":["/images/projects/Project-3IL/resized_s2.webp","/images/projects/Project-3IL/resized_s1.webp","/images/projects/Project-3IL/resized_s3.webp","/images/projects/Project-3IL/resized_s4.webp"],"videos":[],"tags":["VR"],"tech":["Unity","Steam VR","C#"],"developers":["Смирнов М.А.","Новаков Н.Ю.","Егоркин Н.Е.","Валяев Д.А.","Макаров Д.В.","Макущенко М.А.","Баканова М.В.","Шорин И.А.","Иерусалимов И.Д."],"site":"","sourcecode":[{"name":"Source code","link":"https://github.com/RTUITLab/Project-3IL"}],"link":"Project-3IL"},{"#":"","title":"Виртуальный ситуационный центр","---":"","description":"\\n\\nВиртуальный ситуационный центр позволяет множеству людей в любой точке планеты взаимодействовать между собой в единой среде виртуальной реальности. При этом возможно использование любого доступного на данный момент шлема виртуальной реальности.\\n\\nСервисом можно пользоваться без шлема, используя клавиатуру и мышь. В сервисе реализована концепция комнат, к которым подключаются люди, и их общение происходит только внутри данного пространства. Разговоры и обмен файлами не выйдут за пределы выделенного пространства, что обеспечивает конфиденциальность приватного общения. Внутри выделенного виртуального пространства люди могут общаться при помощи голосового чата, могут обмениваться документами из облачных хранилищ (OneDrive, Google drive…), просматривать их, комментировать и обсуждать. В данный момент сервис находится на стадии открытого тестирования.","images":["/images/projects/SC/resized_1.webp","/images/projects/SC/resized_2.webp","/images/projects/SC/resized_s1.webp","/images/projects/SC/resized_s2.webp","/images/projects/SC/resized_s3.webp","/images/projects/SC/resized_s4.webp"],"videos":["https://www.youtube.com/embed/HRFRQa3tMcI"],"tags":["VR","Виртуальное рабочее пространство","Social"],"tech":["NodeJS","ASP. net core","Unreal Engine"],"developers":["Макущенко М.А.","Иванов И.Е."],"site":"","sourcecode":[],"link":"SC"},{"#":"","title":"StuDo","---":"","description":"\\n\\nСервис предназначен для людей, кто желает найти себе партнёров или команду для осуществления своей мечты!\\n\\nОснова сервиса — лента объявлений, с контактами автора, кратким описанием проекта и ролей в нём.\\n\\nНа данный момент проект находится в разработке, в скором времени будет доступен для публичного тестирования.","images":["/images/projects/StuDo/resized_1.webp","/images/projects/StuDo/resized_2.webp","/images/projects/StuDo/resized_3.webp"],"videos":["https://youtu.be/WyLDHwuZedc"],"tags":["Сервис","Web","Mobile","Tool"],"tech":["Vue","ASP.NET core"],"developers":["Миронов Н. М.","Цуканов Д. М.","Яковлев А. А.","Алексеев Н. Е.","Лаптев И. А."],"site":"","sourcecode":[{"name":"Бэкенд","link":"https://github.com/RTUITLab/StuDo-Back"},{"name":"Фронтэнд","link":"https://github.com/RTUITLab/StuDo-Front"},{"name":"IOS","link":"https://github.com/MrFoggz/StuDo-iOS"},{"name":"Android","link":"https://github.com/Suput/StuDo-Android"}],"link":"StuDo"},{"#":"","title":"VRDuel — шутер в виртуальной реальности","---":"","description":"\\n\\n\\nШутер в формате виртуальной реальности, позволяющий оттачивать стрельбу из оружия разных видов и уворачиваться от вражеских пуль. В игре можно бросить вызов сразу нескольким противникам, либо сначала потренироваться в спокойной обстановке. В таком режиме у пользователя есть возможность стрелять по бутылкам в баре и мишеням в тире. На выбор представлено 5 моделей оружия, 4 локации и 3 уровня сложности.\\n","images":["/images/projects/VRDuel/resized_148579235-a4f9e25e-dc3c-49d8-b1a6-7dbabfa52a99.webp","/images/projects/VRDuel/resized_148580147-297f79e2-15e3-4b34-8108-9550982b0fe2.webp","/images/projects/VRDuel/resized_148580160-84ee7b40-83f9-439c-a37e-4e1184bd4dd5.webp","/images/projects/VRDuel/resized_148580059-71f366be-9f6c-44bf-a662-abf7e3e00003.webp"],"videos":[],"tags":["Симулятор","Стрельба","VR","Game"],"tech":["Unity","C#"],"developers":["Егоркин Н.Е.","Шорин И.А."],"site":"","sourcecode":[{"name":"GitHub","link":"https://github.com/RTUITLab/VRDuel"}],"link":"VRDuel"},{"#":"","title":"VRTherapy — Приложение для релаксации в VR со множеством разнообразных активностей","---":"","description":"\\n\\nВ VRTherapy вы можете погрузиться в захватывающую и успокаивающую виртуальную реальность с приятным звуковым сопровождением. Отдохните у виртуального костра, прогуляйтесь по летнему лесу, послушайте шум прибоя или же сыграйте в мини-игру, где вам предстоит управлять бумажным самолётиком на ветру. Данная игра предназначена исключительно для запуска в портативном VR-шлеме для Android, например, Google Cardboard.","images":["/images/projects/VRTherapy/resized_screenshot1.webp","/images/projects/VRTherapy/resized_screenshot2.webp","/images/projects/VRTherapy/resized_screenshot3.webp","/images/projects/VRTherapy/resized_screenshot4.webp"],"videos":[],"tags":["VR","Экспириенс","Game","Simulator","Arcade"],"tech":["Unity","Android","Google VR","Google Cardboard"],"developers":["Баканова М.","Левандровский А.","Матчин А.","Новаков Н."],"site":"","sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/VRTherapy"}],"разработчики":"","link":"VRTherapy"},{"#":"","title":"BunnyHuntVR — Web VR шутер","---":"","description":"\\n\\nVR игра, обороняйте грядки от проворных и назойливых зайцев. Узнайте сколько морковки вы сможете сохранить.","images":["/images/projects/WebVRShoot/resized_BunnyHuntVR.webp","/images/projects/WebVRShoot/resized_BunnyHuntVR2.webp"],"videos":[],"tags":["VR","Game","Shooter"],"tech":["A-Frame","Javascript","Html"],"developers":["Соколов А.В","Иерусалимов И.Д."],"site":"https://webvrshoot.rtuitlab.dev/","sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/WebVRShoot"}],"link":"WebVRShoot"}];
